import api from "../../config/api.js"
import Util from "../../utils/utils"

export default {
  name: 'points',
  data () {
    return {
      token: '',
      currentRoomId: '',
      num: 0,
      owner: 0,
      isSigned: false,
      showSign: false,
      chooicePay: false,
      points: 3883,
      generalTaskList:[
        {path: '/signIn', icon: 'https://img1.aylives.cn/fa21eab5e99549d5b959b112e90008a8.png', content: '', rule: '+36'},
        {path: '/share', icon: 'https://img1.aylives.cn/4827fc99f5cf4cdca26588ed2002ae5e.png', content: '', rule: '+2040'},
        {path: '/attention', icon: 'https://img1.aylives.cn/5e258b091a674bd19a44db401b6294e3.png', content: '', rule: '+612'},
        // {path: '/donateSteps', icon: 'https://img1.aylives.cn/9f16d704770d4923afd7ebfcf7e9205a.png', content: '', rule: '+36'},
        // {path: '/#', icon: 'https://img1.aylives.cn/74384931e9ca4501b903a9f9348b0a3d.png', content: '', rule: '+60'},
        // {path: '/#', icon: 'https://img1.aylives.cn/b5bf6d5c02d4437cae5b981b71cbfbb4.png', content: '', rule: '+40'},
      ],
      planDevList:[
        {path: '/#', icon: 'https://img1.aylives.cn/c9218154e0124ea2a4e756f1a7b7adeb.png', content: '', rule: '+3'},
        {path: '/#', icon: 'https://img1.aylives.cn/7dc8afdf26474cd493ea599d1b1e4cc0.png', content: '', rule: '+3'},
      ]
    }
  },
  created () {
  },
  mounted () {
  },
  methods: {
    tips() {
      this.$toast("", 1500)
    },
    jumpToHappiness() {
      this.token = Util.getCookie('token')
      this.currentRoomId = Util.getCookie('currentRoomId')
      window.location.href = `https://h5.aylives.cn/happy/#/happiness?token=${this.token}&currentRoomId=${this.currentRoomId}`
    },
    getSteps() {
      let donateUrl = "https://h5.aylives.cn/points/#/donateSteps"
      if (Util.isIos()) {
        if(window.webkit && window.webkit.messageHandlers) {
          window.webkit.messageHandlers.openDonateStepH5.postMessage({donateUrl: donateUrl})
        } else {
          this.$toast("App", 1500)
        }
      } else if (Util.isAndroid()) {
        if (window.android &&  window.android.openDonateStepH5) {
          window.android.openDonateStepH5("https://h5.aylives.cn/points/#/donateSteps")
        }  else {
          this.$toast("App", 1500)
        }
      } else {
        this.$toast("App", 1500)
      }
    },
    openDoor() {
      if (Util.isIos()) {
        if(window.webkit && window.webkit.messageHandlers) {
          window.webkit.messageHandlers.openDoor.postMessage()
        }  else {
          this.$toast("App", 1500)
        }
      } else if (Util.isAndroid()) {
        if (window.android &&  window.android.openDoor) {
          window.android.openDoor()
        } else {
          this.$toast("App", 1500)
        }
      } else {
        this.$toast("App", 1500)
      }
    },
    verifyHouse() {
      if (Util.isIos()) {
        if(window.webkit && window.webkit.messageHandlers) {
          window.webkit.messageHandlers.verifyHouse.postMessage()
        } else {
          this.$toast("App", 1500)
        }
      } else if (Util.isAndroid()) {
        if (window.android &&  window.android.verifyHouse) {
          window.android.verifyHouse()
        } else {
          this.$toast("App", 1500)
        }
      } else {
        this.$toast("App", 1500)
      }
    },
    pay() {
      this.chooicePay = true
    },
    cancel() {
      this.chooicePay = false
    },
    payForHouse() {
      if (Util.isIos()) {
        if(window.webkit && window.webkit.messageHandlers) {
          window.webkit.messageHandlers.payHousePropertyFee.postMessage()
        } else {
          this.$toast("App", 1500)
        }
      } else if (Util.isAndroid()) {
        if (window.android &&  window.android.payHousePropertyFee) {
          window.android.payHousePropertyFee()
        } else {
          this.$toast("App", 1500)
        }
      } else {
        this.$toast("App", 1500)
      }
    },
    payForCar() {
      if (Util.isIos()) {
        if(window.webkit && window.webkit.messageHandlers) {
          window.webkit.messageHandlers.payCarPropertyFee.postMessage()
        }  else {
          this.$toast("App", 1500)
        }
      } else if (Util.isAndroid()) {
        if (window.android &&  window.android.payCarPropertyFee) {
          window.android.payCarPropertyFee()
        }  else {
          this.$toast("App", 1500)
        }
      } else {
        this.$toast("App", 1500)
      }
    },
  }
}



